import React, { useEffect, useState, useRef } from "react";



import { getStore,  gen16CodeId,  ObjectKeys, fetchEncryptStream, convertArray2Obj, convertArray2ObjGroupby, hasWritePrivilege } from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import { AddProvider_Cmpt, ProviderItem_Cmpt } from "..";
import {  getall_provider, search_provider } from "../services/server_fetching";
import { getall_subaccount, get_balances_by_type } from "../../CuentasContables/services/server_fetching";
import { PayMultiManual_Cmpt, CollectMultiManual_Cmpt } from "../../Cobros";
import { useNavigate } from "react-router";



const SearchInput = SearchInput_Cmpt();
const AddProvider = AddProvider_Cmpt();


const ProviderItem = ProviderItem_Cmpt();
const PayMultiManual = PayMultiManual_Cmpt();
const CollectMultiManual = CollectMultiManual_Cmpt();




const ProviderDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [loading, set_loading] = useState(false);


	const navigate = useNavigate();


	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			get_balances_by_type("global_accounts","global_account_params",getallAccount);
			
		}
	});


	
	const getallAccount =  () => {
		getall_subaccount(getallProvider);
	}

	const getallProvider =  () => {
		getall_provider(cb_result);
	}

	


	



	
	const openAddView =  () => {
		//setAddview(true)
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddProvider confimr={handle_getall_provider} />
		OpenModal(dataModal);
	}



	const openMultiPay =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<PayMultiManual confimr={()=>{}} />
		OpenModal(dataModal);
	}

	const openMultiCollect =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<CollectMultiManual confimr={()=>{}} />
		OpenModal(dataModal);
	}
	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_provider =  (v) => {
		set_loading(true)
		search_provider(cb_result)
	}


	const handle_getall_provider =  (v) => {
		getall_provider(cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	const goToFutureChecks =  (v) => {
		navigate({
			pathname: "/pay-checks-on-future" 
		});
	}
	

	

	return (

	<div className="">
		<div className="accounts_header">
			<h2>Provedores o Clientes</h2>
			{hasWritePrivilege()?
			<div className="_dsplFlx ">
				
				<p className="group_title addBtn purple" onClick={openAddView}>
					Agregar Provedores o Clientes
				</p>
				
				<p className="group_title addBtn purple" onClick={openMultiPay}>
					Pagar a Provedores
				</p>
				<p className="group_title addBtn purple" onClick={openMultiCollect}>
					Cobrar a Clientes
				</p>
				<p className="group_title addBtn purple" onClick={goToFutureChecks}>
					Cheques a Futuro
				</p>
					
			
			</div>
			:null}
			<div className="_dsplFlx " style={{marginTop:14}}>
				<SearchInput
					placeholder={"Buscar ...."} 
					width={360}
					obs={set_obs}
					fetchData={handle_search_provider}
					loading={loading}
					change={handleChange}
					callempty={handle_getall_provider}
					keyFlds={"search_prov"}
				/>
				<div className="flexSpace"/>
				<div className="_dsplFlx ">
					<div className="total_comprobante_title">
						No de Provedores: 
					</div>
					<div className="total_comprobante gray">
						<span>
							{ObjectKeys(getStore("providers_list"))?.length}
						</span>
					</div>
					
				</div>
			</div>
		</div>
		
			<div className="providers_container scollVh">
				{getStore("providers_list") && ObjectKeys(getStore("providers_list"))?.map(cid=>{
					return (
						<ProviderItem 
							data={getStore("providers_list")[cid]} 
							elmId={cid} 
							key={cid}
							handleRefreshAll={handle_getall_provider}
						/>
					)
				})}
			</div>
		
		</div>
	);
};

export default ProviderDashboard;
