import { fetchEncryptStream, updStore, getStore, gen16CodeId, convertArray2ObjGroupby, convertArray2Obj, ObjectKeys, callbackOnMemory, convertObj2Array, qryStore } from "../../../stores/Utils";

export const getall_provider = async (cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.all_prov_cust};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("providers_list", sortProviders(convertObj2Array(td),"code"));
        updStore("providers_ids", convertArray2Obj(convertObj2Array(td),"providerId"));
        cb && cb();
    }
};




