export const get_filter_by_acc = async (frm,cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.find_prov_cust};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        ":provider": frm.provider
    }
    if(frm?.subProvider){
        bdyq2["params"][":sub_provider"] = frm?.subProvider;
        bdyq2["queryString"] += ' AND subProvider contain :sub_provider';
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td && !td.error) {
        updStore("submayor_list", td);
        updStore("submayor_group", GroupbyAcc(td));
    }
    cb && cb();
};




export function GroupbyAcc(arr, isPay) {
    let key =  "provider";
    var obj = {
        "operations":[]
    };
    let balance = 0;

    let arr1 = arr && ObjectKeys(arr)
    if (arr1.length > 0) {
      arr1.map((ele) => {
        let s = arr[ele];
        if(s["type"]){
            if(isPay){
                balance += s["type"] === "debit" ? s["amount"] * -1 : s["amount"] * 1;
            }
            else{
                balance += s["type"] === "credit" ? s["amount"] * -1 : s["amount"] * 1;
            }
            obj["operations"].push(convert4acc(s));
        }
      });
    } else {
      obj = null;
    }
    return obj;
  }



  export function convert4acc(s) {
    let nItm = {};
    nItm["provider"] =  getStore("providers_ids")?.[s["provider"]];
    nItm["sub_provider"] = getStore("sub_providers_ids")?.[s["subProvider"]];
    nItm["comprobanteId"] = s["comprobanteId"];
    nItm["document"] = s["document"];
    nItm["date"] = s["date"];
    if(s["type"]==="credit"){
        nItm["debit"] = 0;
        nItm["credit"] = s["amount"];
        
    }
    else if(s["type"]==="debit"){
        nItm["credit"] = 0;
        nItm["debit"] = s["amount"];
    }
    return nItm;
  }



  
//  sorting episodes comparing inputs to save in cache
export const sortProviders = (td, key) =>
callbackOnMemory(
	"sortProviders",
	[td],
	() => {
        let order = true;
		let sortedA = td && td.sort((a, b) => {
			let A = a[key] * 1;
			let B = b[key] *1;
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);


